import React from 'react';
import { Modal, Tabs } from 'antd';

import './more_popup.less';

const TabPane = Tabs.TabPane;
class MorePopup extends React.Component {

    state = {
        actionType: '',
        actionName: ''
    }

    //
    handleOk=()=>{
        let {closeHandle} = this.props;
        closeHandle && closeHandle();
    }

    //
    handleCancel=()=>{
        let {closeHandle} = this.props;
        closeHandle && closeHandle();
    }

    componentDidUpdate() {
        let {actionHandle, closeHandle} = this.props;
        let action = this.state;
        closeHandle && closeHandle();
        actionHandle && actionHandle(action);
    }


    render() {
        const willOrderMore = ['',''];
        const willOrderOperate = ['', '', '', '', '', '', '', '', '', ''];
        const didOrderOperate = ['', '', '', '', '', '', '', '']
        const booking = this.props.booking;
        return <Modal 
            title=""
            visible={true}
            maskClosable={false}
            onOk={this.handleOk}
            footer={null}
            onCancel={this.handleCancel}
            okText='' 
            cancelText=''
            width={840}
            wrapClassName="dishes-more-popup-modal"
        >
            <Tabs 
                defaultActiveKey="0" 
                onChange={this.callback}
                tabBarStyle={{
                    display: booking ? 'none' : 'block'
                }}
            >
                <TabPane tab="" key="0">
                    <div className="guige">
                        {willOrderMore.map((actionName, index) => {
                            return <div 
                                key={index}
                                className="more-btn-171 btn-active" 
                                onClick={() => {
                                this.setState({
                                    actionType: 'willOrder',
                                    actionName
                                });
                            }}>{actionName}</div>
                        })}
                        <div className="batch-operate">
                        <span className="line"></span>
                        <span className="circle"></span>
                        <span className="text"></span>
                        <span className="circle"></span>
                        <span className="line"></span>
                        </div>
                        <ul className="batch-btn">
                        {willOrderOperate.map((actionName, index)=>{
                            return <li 
                                key={index} 
                                className="batch-btn-171" 
                                onClick={() => {
                                    this.setState({
                                        actionType: 'willOrder',
                                        actionName
                                    });
                                }}
                            >
                                {actionName}
                            </li>
                        })}
                        </ul>
                    </div>
                </TabPane>
                <TabPane tab="" key="1">
                    <div className="guige">
                        <ul className="batch-btn">
                        {didOrderOperate.map((actionName, index)=>{
                            return <li 
                                key={index} 
                                className="batch-btn-171" 
                                onClick={() => {
                                    this.setState({
                                        actionType: 'didOrder',
                                        actionName
                                    });
                                }}
                            >
                                {actionName}
                            </li>
                        })}
                        </ul>
                    </div>
                </TabPane>
            </Tabs>
        </Modal>
    }
}



export default MorePopup;



