import React, { Component } from 'react';
import { Modal, Button } from 'antd';
import './prompt_content.css';


/**
 * message
 * @param {
 * title:
 * isOneFooter: ture       false   
 *{title:"",isOneFooter:false,conText:"",okClick:function(){console.log(111)},cancelClick:function(){console.log(222)}}
 * conText:
 * okText:
 * cancelText:
 * }
 * messsage:{title:"",isOneFooter:true,conText:""}
 * messsage:{title:"",isOneFooter:false,conText:"",okText:"",cancelText:""}
 * messsage:{title:"",isOneFooter:true,conText:"A03,A04,A05"}
 *
 *
 */
class PromptCommon extends Component {

  handleCancel = (e) => {
    if(this.props.message.cancelClick){
        this.props.message.cancelClick();
    }

  }

  handleOk = ()=>{
    if(this.props.message.okClick){
        this.props.message.okClick();
    }

  }

  componentDidMount() {
    this.refs.conText.innerHTML=this.props.message.conText;
  }
  render() {
        let isFooter,footerBlock;

        if(this.props.message.isOneFooter){
            isFooter = null;
            footerBlock = <div className="Prompt_button" onClick={this.handleOk}></div>
        }else{
            isFooter = [
            <Button key="cancel" onClick={this.handleCancel}>{this.props.message.cancelText||''}</Button>,
            <Button key="ok" onClick={this.handleOk}>
             {this.props.message.okText||''}
            </Button>,
          ]
        }
    return <Modal title={this.props.message.title} visible={true} maskClosable={false}
          onCancel={this.handleCancel}  footer={isFooter} wrapClassName="vertical-center-modal"
          width={600}>
      <div className="Prompt_content" ref='conText'>

      </div>
      {footerBlock}
    </Modal>
  }
}

export default PromptCommon;



